.nav-link {
  position: relative;
  text-decoration: none;
  color: currentColor;
}
.nav-link:focus, .nav-link:active, .nav-link:visited {
  outline: none;
}
.nav-link:focus-visible {
  outline: 1px solid var(--color-primary);
}
.nav-link--underline-link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -8px;
  height: 2px;
  width: 100%;
  background-color: currentColor;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease-in-out;
}
.nav-link--underline-link:hover::after {
  transform: scaleX(1);
  transition: transform 0.3s ease-in-out;
}

